export default "import { Button, Cell, Checkbox, FormControl, Grid, HFlow, MaskedTextField, Radio, TextField } from 'bold-ui'\nimport React, { useState } from 'react'\n\nfunction FormDemo() {\n  const [formState, setFormState] = useState({\n    name: '',\n    phone: '',\n    email: '',\n    color: '',\n    agreed: false,\n  })\n\n  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {\n    e.preventDefault()\n    alert(JSON.stringify(formState, null, 2))\n  }\n\n  const handleChange = (name: string) => (e: React.ChangeEvent<HTMLInputElement>) => {\n    const el = e.target\n\n    setFormState(state => ({\n      ...state,\n      [name]: el.type === 'checkbox' ? el.checked : el.value,\n    }))\n  }\n\n  return (\n    <>\n      <form onSubmit={handleSubmit}>\n        <Grid wrap>\n          <Cell xs={6}>\n            <TextField\n              name='name'\n              label='Name'\n              placeholder='Enter your full name'\n              value={formState.name}\n              onChange={handleChange('name')}\n              required\n            />\n          </Cell>\n          <Cell xs={6}>\n            <MaskedTextField\n              name='phone'\n              label='Phone number'\n              mask={['(', /\\d/, /\\d/, ')', ' ', /\\d/, /\\d/, /\\d/, /\\d/, '-', /\\d/, /\\d/, /\\d/, /\\d/]}\n              placeholder='(__) ____-____'\n              value={formState.phone}\n              onChange={handleChange('phone')}\n              required\n            />\n          </Cell>\n          <Cell xs={6}>\n            <TextField\n              name='email'\n              label='E-mail'\n              type='email'\n              placeholder='Enter your e-mail'\n              value={formState.email}\n              onChange={handleChange('email')}\n            />\n          </Cell>\n          <Cell xs={6}>\n            <FormControl label='Favorite color'>\n              <HFlow>\n                <Radio name='color' value='red' label='Red' onChange={handleChange('color')} />\n                <Radio name='color' value='green' label='Green' onChange={handleChange('color')} />\n                <Radio name='color' value='blue' label='Blue' onChange={handleChange('color')} />\n              </HFlow>\n            </FormControl>\n          </Cell>\n          <Cell xs={12}>\n            <Checkbox name='agreed' label='I agree to the terms of use' onChange={handleChange('agreed')} />\n          </Cell>\n          <Cell xs={12}>\n            <HFlow justifyContent='flex-end'>\n              <Button type='reset' skin='outline'>\n                Reset\n              </Button>\n              <Button type='submit' kind='primary'>\n                Submit\n              </Button>\n            </HFlow>\n          </Cell>\n        </Grid>\n      </form>\n\n      <pre>{JSON.stringify(formState, null, 2)}</pre>\n    </>\n  )\n}\n\nexport default FormDemo\n";