export default "import { Button, Dropdown, DropdownDivider, DropdownItem, Tooltip } from 'bold-ui'\nimport React, { useRef, useState } from 'react'\n\nexport default function DropdownExample() {\n  const buttonRef = useRef<HTMLButtonElement>()\n\n  const [open, setOpen] = useState(false)\n  const handleClick = () => setOpen(true)\n  const handleClose = () => {\n    setOpen(false)\n    buttonRef.current.focus()\n  }\n\n  return (\n    <>\n      <Button innerRef={buttonRef} onClick={handleClick} size='small' kind='primary' skin='outline'>\n        Options\n      </Button>\n\n      <Dropdown anchorRef={buttonRef} open={open} onClose={handleClose} popperProps={{ placement: 'bottom' }}>\n        <DropdownItem onClick={console.log}>Item #1</DropdownItem>\n        <Tooltip text='Disabled item'>\n          <DropdownItem onClick={console.log} disabled>\n            Item #2\n          </DropdownItem>\n        </Tooltip>\n        <DropdownDivider />\n        <DropdownItem component='a' href='/'>\n          Link item\n        </DropdownItem>\n        <DropdownItem type='danger' onClick={console.log}>\n          Item #3\n        </DropdownItem>\n      </Dropdown>\n    </>\n  )\n}\n";