export default "import { Button, DataTable, Icon } from 'bold-ui'\nimport React, { useState } from 'react'\n\ninterface RowType {\n  id: number\n  name: string\n  age: number\n}\n\nfunction DataTableExample() {\n  const [sort, setSort] = useState(['id'])\n\n  const rows = allRows\n    // Naive sorting for example purposes:\n    .sort((a, b) => {\n      if (sort[0] === 'id') {\n        return a.id - b.id\n      }\n      if (sort[0] === '-id') {\n        return b.id - a.id\n      }\n      return 0\n    })\n\n  return (\n    <DataTable<RowType>\n      rows={rows}\n      sort={sort}\n      onSortChange={setSort}\n      loading={false}\n      columns={[\n        {\n          name: 'id',\n          header: 'ID',\n          sortable: true,\n          render: item => item.id,\n        },\n        {\n          name: 'name',\n          header: 'Name',\n          render: item => item.name,\n        },\n        {\n          name: 'age',\n          header: 'Age',\n          render: item => item.age,\n        },\n        {\n          name: 'actions',\n          align: 'right',\n          render: item => (\n            <Button size='small' skin='ghost'>\n              <Icon icon='penOutline' />\n            </Button>\n          ),\n        },\n      ]}\n    />\n  )\n}\n\nexport default DataTableExample\n\n// Fake data to populate table\nlet id = 1\nconst allRows: RowType[] = Array(3)\n  .fill(true)\n  .reduce(\n    curr => [\n      ...curr,\n      { id: id++, name: 'MARIA MACHADO DE JESUS', age: 42 },\n      { id: id++, name: 'JOSÉ DA SILVA MOREIRA', age: 34 },\n      { id: id++, name: 'ALICE BARBOSA', age: 27 },\n    ],\n    [] as RowType[]\n  )\n";